.container {
  height: 100%;
  width: 100%;
}
.container .section {
  background: #fff;
  margin-bottom: 10px;
  padding: 10px 8px;
}
.container .banners-box {
  height: 155px;
  touch-action: none;
  box-sizing: border-box;
  background: #fff;
  margin-bottom: 10px;
  padding: 10px 8px;
}
.container .banners-box img {
  width: 100%;
  height: 36vw;
}
.container .menu {
  display: flex;
  background: #fff;
  padding: 20px 0;
  margin-bottom: 10px;
}
.container .menu > div {
  flex-grow: 1;
  text-align: center;
}
.container .menu > div a {
  display: block;
  color: #828282;
}
.container .menu > div .icon {
  font-size: 20px;
  margin-bottom: 5px;
}
.container .hot-singer-box {
  background: #fff;
  margin-bottom: 10px;
  padding: 10px 8px;
}
.container .hot-singer-box .title-box {
  display: flex;
  justify-content: space-between;
}
.container .hot-singer-box .title-box a {
  color: #1890ff;
}
.container .hot-singer-box ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 15px;
}
.container .hot-singer-box ul li {
  width: 21%;
  margin-bottom: 10px;
}
.container .hot-singer-box ul li .singer-box {
  display: block;
  color: #000;
  text-align: center;
  font-size: 12px;
}
.container .hot-singer-box ul li .singer-box img {
  width: 100%;
  border-radius: 50%;
}
.container .hot-singer-box ul li .singer-box div {
  margin-top: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #333;
}
.container .recommend-box {
  background: #fff;
  margin-bottom: 10px;
  padding: 10px 8px;
}
.container .recommend-box ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 10px;
}
.container .recommend-box ul li {
  position: relative;
  width: 32%;
  margin-bottom: 10px;
}
.container .recommend-box ul li .sheet-box {
  display: block;
  color: #000;
  font-size: 12px;
}
.container .recommend-box ul li .sheet-box img {
  width: 100%;
  border-radius: 5px;
}
.container .recommend-box ul li .sheet-box div {
  height: 28px;
  margin-top: 5px;
  line-height: 1.2;
  overflow: hidden;
  color: #333;
}
.container .recommend-box ul li .sheet-box .playCount {
  position: absolute;
  right: 5px;
  top: 5px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
}
